export function stripUnit(v, u) {
    if (!v) {
        return 0;
    }
    return v.div(u).value;
}
export function formatNumber(x) {
    return x.toLocaleString("en-US", {
        useGrouping: false,
        maximumFractionDigits: 3,
    });
}
function formatSeconds(value) {
    const seconds = Math.floor(value % 60);
    value -= seconds;
    const minutes = Math.floor((value % 3600) / 60);
    value -= minutes * 60;
    const hours = Math.floor(value / 3600);
    if (hours > 0) {
        return hours + ":" + minutes.toString().padStart(2, "0") + ":" + seconds.toString().padStart(2, "0");
    }
    return minutes + ":" + seconds.toString().padStart(2, "0");
}
export class SecondFormatter {
    formatValue(value) {
        return formatSeconds(value);
    }
    formatUnit(unit) { return ""; }
}
export class MinuteFormatter {
    formatValue(value) {
        return formatSeconds(value * 60);
    }
    formatUnit(unit) { return ""; }
}
export class MeterFormatter {
    formatValue(value) {
        if (value > 1000) {
            const km = value / 1000;
            return km.toLocaleString("en-US", {
                useGrouping: false,
                maximumFractionDigits: 2,
            }) + " km";
        }
        return Math.round(value).toString() + " m";
    }
    formatUnit(unit) { return ""; }
}
export function makeRoundedNumberFormatter(digits) {
    function formatValue(value) {
        return value.toLocaleString("en-US", {
            useGrouping: false,
            maximumFractionDigits: digits,
        });
    }
    return { formatValue };
}
